import { ClassPropertyDescription } from './classPropertyDescription';

export class ClassDescription {

    associations?: any;
    childassociations?: any;
    defaultAspects?: any;
    description?: string;
    isAspect?: boolean;
    isContainer?: boolean;
    name?: string;
    parent?: any;
    properties?: {
        [key: string]: ClassPropertyDescription;
    };
    title?: string;
    url?: string;

    constructor(input?: any) {

        if (input) {
            Object.assign(this, input);
        }
    }
}
